<template>
  <div>
    <el-tabs v-model="activeName">
      <!-- 功能页面 -->
      <el-tab-pane label="功能页面" name="0">
        <el-table :data="pageList" style="width: 100%">
          <el-table-column prop="linkName" label="功能页面名称"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="selectPage(scope.row)">选择链接</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <!-- 商品分组 -->
      <el-tab-pane label="商品分组" name="1">
        <el-table :data="classList" style="width: 100%;" row-key="id" border :tree-props="{children: 'productCategoryShopList'}">
          <el-table-column prop="name" label="分类名称"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="selectClass(scope.row)">选择链接</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div style="padding-top:20px;">
          <el-pagination
            @size-change="classSizeChange"
            @current-change="classCurrentChange"
            :page-sizes="[5,10,15,20]"
            :page-size="classForm.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="classListTotal">
          </el-pagination>
        </div>
      </el-tab-pane>
      <!-- 商品详情 -->
      <el-tab-pane label="商品详情" name="2">
        <div>
          <i class="el-icon-search"></i>
          <span>筛选搜索</span>
          <el-button style="float: right" @click="getCommodity" type="primary" size="small">查询</el-button>
          <el-button style="float: right;margin-right: 15px" @click="resetCommodity" size="small">重置</el-button>
        </div>
        <div style="margin-top: 15px">
          <el-form :inline="true" :model="commodityForm" size="small" label-width="140px">
            <el-form-item label="商品名称：">
              <el-input style="width: 203px" v-model="commodityForm.name" clearable placeholder="分类名称"></el-input>
            </el-form-item>
            <el-form-item label="商品类目：">
              <el-cascader
                v-model="commodityForm.shopCategoryIdArr"
                :options="category"
                :props="{ checkStrictly: true }"
                clearable></el-cascader>
            </el-form-item>
          </el-form>
        </div>
        <div>
          <el-table border :data="commodityList" style="width: 100%">
            <el-table-column label="商品图片">
              <template slot-scope="scope">
                <el-image style="width: 50px; height: 50px" :src="scope.row.imageUrl" fit="fill"></el-image>
              </template>
            </el-table-column>
            <el-table-column property="name" label="商品名称"></el-table-column>
            <el-table-column property="maxPrice" label="价格"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button type="text" size="small" @click="selectCommodity(scope.row)">选择链接</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div style="padding-top:20px;">
          <el-pagination
            @size-change="commoditySizeChange"
            @current-change="commodityCurrentChange"
            :page-sizes="[5,10,15,20]"
            :page-size="commodityForm.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="commodityListTotal">
          </el-pagination>
        </div>
      </el-tab-pane>
      <!-- 装修页面 -->
      <el-tab-pane label="装修页面" name="3">
        <div>
          <i class="el-icon-search"></i>
          <span>筛选搜索</span>
          <el-button style="float: right" @click="getWePage" type="primary" size="small">查询</el-button>
          <el-button style="float: right;margin-right: 15px" @click="resetWePage" size="small">重置</el-button>
        </div>
        <div style="margin-top: 15px">
          <el-form :inline="true" :model="wepageForm" size="small" label-width="140px">
            <el-form-item label="微页面名称：">
              <el-input style="width: 203px" v-model="wepageForm.pageName" clearable placeholder="分类名称"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <el-table :data="wepageList" style="width: 100%">
          <el-table-column prop="vpageName" label="页面名称"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="selectVPage(scope.row)">选择链接</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div style="padding-top:20px;">
          <el-pagination
            @size-change="wepageSizeChange"
            @current-change="wepageCurrentChange"
            :page-sizes="[5,10,15,20]"
            :page-size="wepageForm.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="wepageListTotal">
          </el-pagination>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { 
  getShopNavigationInfoList,
  getSystemLinkList
} from '@/api/shop/navBar'
import { getProductCategoryShopAllList , getProductCategoryInfoPageList } from '@/api/commodity/commodityClassify'
import { getProductInfoList } from '@/api/commodity/commodityList'
import { shopVpagePage } from '@/api/shop/shopDecoration'
export default {
  data(){
    return{
      activeName:'0',
      category:[],
      listForm:{
        pageNum:1,
        pageSize:5
      },
      //功能页面
      pageList:[],
      // 商品分组
      classForm:{
        pageNum:1,
        pageSize:5,
      },
      classList:[],
      classListTotal:0,
      // 商品详情参数
      category:[],
      commodityForm:{
        pageNum:1,
        pageSize:5,
        tabType:1,
        shopCategoryIdArr:[]
      },
      commodityList:[],
      commodityListTotal:0,
      //微页面
      wepageForm:{
        pageNum:1,
        pageSize:5,
        pageName:''
      },
      wepageList:[],
      wepageListTotal:0
    }
  },
  created(){
    this.commodityForm.shopId = this.shop.shopId
    this.wepageForm.shopId = this.shop.shopId
    this.classForm.shopId = this.shop.shopId
    this.getSystem()
    this.getClass()
    this.getCommodity()
    this.getCategory()
    this.getWePage()
  },
  methods:{
    // 功能页面 - 获取列表
    getSystem(){
      getSystemLinkList().then(res=>{
        console.log('获取系统列表',res)
        this.pageList = res.data
        this.pageList.forEach(e => e.select = false)
      })
    },
    // 功能页面 - 选择链接
    selectPage(e){
      let selectObj = {
        linkUrl:e.linkUrl,
        parmas:'',
        linkName:`功能页面 - ${e.linkName}`
      }
      this.$emit('subMitNav',selectObj);
    },

    // 商品分组 - 获取数据
    getClass(){
      getProductCategoryInfoPageList(this.classForm).then(res=>{
        console.log('获取商品分组',res)
        this.classList = res.data.list
        this.classListTotal = res.data.total
      })
    },
    // 商品分组 - 选择
    selectClass(e){
      console.log('e',e)
      let selectObj = {
        linkUrl:'/goodsCateList',
        linkName:`商品分组 - ${e.name}`,
        title:e.name
      }
      if(e.level == 1) selectObj.parmas = 'shopCategoryParentId='+e.id
      if(e.level == 2) selectObj.parmas = 'shopCategoryId='+e.id
      this.$emit('subMitNav',selectObj);
    },
    // 商品分组 - 分页
    classSizeChange(val) {
      this.classForm.pageSize = val;
      this.getClass()
    },
    // 商品分组 - 分页
    classCurrentChange(val) {
      this.classForm.pageNum = val;
      this.getClass()
    },
    
    // 商品详情 - 获取商品列表
    getCommodity(){
      this.commodityForm.shopCategoryId = this.commodityForm.shopCategoryIdArr[1] || ''
      this.commodityForm.shopCategoryPid = this.commodityForm.shopCategoryIdArr[0] || ''
      getProductInfoList(this.commodityForm).then(res=>{
        console.log('商品列表',res)
        this.commodityList = res.data.list
        this.commodityList.forEach(e=>e.select = false)
        this.commodityListTotal = res.data.total
      })
    },
    // 商品详情 - 清空搜索
    resetCommodity(){
      this.commodityForm.name = '';
      this.commodityForm.shopCategoryIdArr = []
      this.commodityForm.shopCategoryId = ''
      this.commodityForm.shopCategoryPid = ''
      this.getCommodity();
    },
    //商品详情 - 获取全部分类
    getCategory(){
      getProductCategoryShopAllList(this.shop.shopId).then(res=>{
        console.log('获取店铺分类',res)
        this.category = res.data
      })
    },
    // 商品详情 - 选择链接
    selectCommodity(e){
      let selectObj = {
        linkUrl:'/productDetail',
        parmas:'productId='+e.id,
        linkName:`商品详情 - ${e.name}`
      }
      this.$emit('subMitNav',selectObj);
    },
    // 商品详情 - 分页
    commoditySizeChange(val) {
      this.commodityForm.pageSize = val;
      this.getCommodity()
    },
    // 商品详情 - 分页
    commodityCurrentChange(val) {
      this.commodityForm.pageNum = val;
      this.getCommodity()
    },


    // 微页面 - 获取数据
    getWePage(){
      shopVpagePage(this.wepageForm).then(res=>{
        console.log('微页面列表',res)
        this.wepageList = res.data.list
        this.wepageListTotal = res.data.total
      })
    },
    // 微页面 - 重置
    resetWePage(){
      this.wepageForm.pageName = ''
      this.getWePage();
    },
    // 微页面 - 选中
    selectVPage(e){
      let selectObj = {
        linkUrl:'/wepage',
        parmas:e.vpageId,
        linkName:`装修页面 - ${e.vpageName}`
      }
      this.$emit('subMitNav',selectObj);
    },
    // 微页面 - 分页
    wepageSizeChange(val) {
      this.wepageForm.pageSize = val;
      this.getWePage()
    },
    // 微页面 - 分页
    wepageCurrentChange(val) {
      this.wepageForm.pageNum = val;
      this.getWePage()
    }
  },
  computed: {
    ...mapState([
      'shop',
      'user'
    ]),
  },
}
</script>
<style lang="less" scoped>

</style>